"use client";

import React, { useState } from "react";

// --- TYPES ---
interface Experience {
  id: string;
  company: string;
  location: string;
  startDate: string;
  endDate: string;
  role: string;
  description: string;
}

// --- INITIAL DATA ---
const INITIAL_EXPERIENCES: Experience[] = [
  {
    id: "1",
    company: "Pixetive Agency",
    location: "Dhaka",
    startDate: "Jan 2019",
    endDate: "Oct 2020",
    role: "Visual Designer",
    description:
      "Visual Designers Are Conceptual Artists Who Design Comprehensive Brand Imagery, Story Visuals And Artistic Visions For Design Projects. Visual Designers Work With Clients To Assess Their Goals And Ideas Regarding Marketing Plans, Advertising Initiatives And Company Or Product Branding.",
  },
  {
    id: "2",
    company: "Mickeysoft Agency",
    location: "New York",
    startDate: "May 2020",
    endDate: "Dec 2022",
    role: "Interaction Designer",
    description:
      "Visual Designers Are Conceptual Artists Who Design Comprehensive Brand Imagery, Story Visuals And Artistic Visions For Design Projects. Visual Designers Work With Clients To Assess Their Goals And Ideas Regarding Marketing Plans, Advertising Initiatives And Company Or Product Branding.",
  },
  {
    id: "3",
    company: "Usernet Solution",
    location: "London",
    startDate: "Jan 2022",
    endDate: "Oct 2024",
    role: "Sr. Product Designer",
    description:
      "Visual Designers Are Conceptual Artists Who Design Comprehensive Brand Imagery, Story Visuals And Artistic Visions For Design Projects. Visual Designers Work With Clients To Assess Their Goals And Ideas Regarding Marketing Plans, Advertising Initiatives And Company Or Product Branding.",
  },
];

// --- TimelineItem ---
interface TimelineItemProps {
  data: Experience;
  isLast: boolean;
  index: number;
}

const TimelineItem: React.FC<TimelineItemProps> = ({ data, isLast, index }) => {
  return (
    <div
      className="grid grid-cols-1 md:grid-cols-[1fr_auto_1fr] gap-x-8 group"
      style={{ animationDelay: `${index * 100}ms` }}
    >
      {/* Left: Company */}
      <div className="md:text-right mb-2 md:mb-0 md:pt-1">
        <h3 className="font-serif font-bold text-foreground text-xl tracking-tight leading-tight">
          {data.company}
          {data.location && (
            <span className="text-muted-foreground font-normal">, {data.location}</span>
          )}
        </h3>
        <p className="text-xs font-semibold text-muted-foreground uppercase tracking-widest mt-2 font-sans">
          {data.startDate} - {data.endDate}
        </p>
      </div>

      {/* Center: Timeline graphic */}
      <div className="hidden md:flex flex-col items-center relative mx-4">
        {/* The Circle */}
        <div className="w-5 h-5 bg-gradient-to-tr from-orange-500 to-pink-500 rounded-full border-4 border-background z-10 shadow group-hover:scale-110 transition-all duration-300"></div>
        {/* The Dotted Line */}
        {!isLast && (
          <div className="absolute top-5 bottom-0 left-1/2 -translate-x-1/2 w-px border-l-2 border-dotted border-accent/20 -mb-8 group-hover:border-accent/40 transition-colors duration-300 h-[calc(100%+2rem)]"></div>
        )}
      </div>

      {/* Right: Role and Description */}
      <div className="pb-12 md:pb-20 relative pl-4 md:pl-0 border-l-2 border-dotted border-accent/15 md:border-l-0 ml-2 md:ml-0">
        {/* Mobile Circle indicator */}
        <div className="absolute -left-[5px] top-3 w-2.5 h-2.5 bg-gradient-to-tr from-orange-500 to-pink-500 rounded-full md:hidden"></div>
        <h4 className="font-serif font-bold text-xl text-foreground mb-3 group-hover:text-orange-500 transition-colors">
          {data.role}
        </h4>
        <p className="text-sm text-muted-foreground leading-relaxed font-sans text-justify md:text-left">
          {data.description}
        </p>
      </div>
    </div>
  );
};

// --- Main ExperienceSection ---
const ExperienceSection = () => {
  const [experiences] = useState<Experience[]>(INITIAL_EXPERIENCES);

  return (
    <section id="experience" className="py-20 bg-card relative">
      {/* Subtle Decorations */}
      <div className="pointer-events-none absolute inset-0 opacity-[0.015] z-0" style={{ backgroundImage: "radial-gradient(var(--accent) 1px,transparent 1px)", backgroundSize: "20px 20px" }}></div>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        <div className="text-center mb-20 relative">
          <h2 className="font-serif text-4xl md:text-5xl font-bold text-foreground mb-6 relative inline-block">
            My Work Experience
            <span className="absolute -bottom-2 left-0 w-full h-1 bg-gradient-to-r from-transparent via-orange-400 to-transparent opacity-30"></span>
          </h2>
          <p className="text-muted-foreground max-w-2xl mx-auto text-base mt-3">
            A chronological overview of my professional journey, roles, and achievements in the design and development industry.
          </p>
        </div>
        <div className="relative z-10">
          {experiences.map((exp, index) => (
            <TimelineItem
              key={exp.id}
              data={exp}
              isLast={index === experiences.length - 1}
              index={index}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default ExperienceSection;

