"use client";

import Image from "next/image";
import { Github, ExternalLink } from "lucide-react";

const projects = [
  {
    id: 1,
    title: "E-Commerce Platform",
    description:
      "Full-stack MERN application with Stripe integration, JWT authentication, and admin dashboard.",
    tech: ["React.js", "Node.js", "MongoDB", "Stripe"],
    image: "/nextjs-developer-working.jpg",
    github: "#",
    demo: "#",
  },
  {
    id: 2,
    title: "Task Management System",
    description:
      "Real-time collaborative task management app with WebSocket integration and Redux state management.",
    tech: ["React.js", "Express.js", "MongoDB", "Redux"],
    image: "/nodejs-backend-development.jpg",
    github: "#",
    demo: "#",
  },
  {
    id: 3,
    title: "Analytics Dashboard",
    description:
      "Data visualization dashboard with responsive charts, filters, and real-time updates.",
    tech: ["Next.js", "TypeScript", "Tailwind CSS", "REST API"],
    image: "/react-hooks-code.png",
    github: "#",
    demo: "#",
  },
  {
    id: 4,
    title: "Social Media App",
    description:
      "Full-stack social platform with authentication, feed, profiles, and real-time notifications.",
    tech: ["React.js", "Firebase", "Redux", "Tailwind CSS"],
    image: "/tailwind-css-design.png",
    github: "#",
    demo: "#",
  },
  {
    id: 5,
    title: "Weather App",
    description:
      "Weather forecasting application with geolocation, multiple weather sources, and detailed analytics.",
    tech: ["React.js", "REST API", "Tailwind CSS", "Context API"],
    image: "/mongodb-database-design.jpg",
    github: "#",
    demo: "#",
  },
  {
    id: 6,
    title: "Blog Platform",
    description:
      "Content management system with markdown support, categories, comments, and admin panel.",
    tech: ["Next.js", "MongoDB", "Tailwind CSS", "Authentication"],
    image: "/typescript-programming.png",
    github: "#",
    demo: "#",
  },
];

const ProjectsSection = () => {
  return (
    <section id="projects" className="py-20 bg-card">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-4xl font-bold mb-12 text-center">Featured Projects</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {projects.slice(0, 6).map((project) => (
            <div
              key={project.id}
              className="group rounded-lg overflow-hidden bg-background shadow-md hover:shadow-lg transition-all"
            >
              <div className="relative h-48 overflow-hidden">
                <Image
                  src={project.image || "/placeholder.svg"}
                  alt={project.title}
                  width={400}
                  height={200}
                  className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
                />
              </div>
              <div className="p-6 space-y-4">
                <h3 className="text-xl font-bold group-hover:text-orange-500 transition-colors">
                  {project.title}
                </h3>
                <p className="text-muted-foreground text-sm">{project.description}</p>
                <div className="flex flex-wrap gap-2">
                  {project.tech.map((tech) => (
                    <span key={tech} className="px-2 py-1 bg-accent text-xs rounded">
                      {tech}
                    </span>
                  ))}
                </div>
                <div className="flex gap-3 pt-4">
                  <a
                    href={project.github}
                    className="inline-flex items-center gap-2 text-sm font-medium text-muted-foreground hover:text-foreground"
                  >
                    <Github className="w-4 h-4" /> Source
                  </a>
                  <a
                    href={project.demo}
                    className="inline-flex items-center gap-2 text-sm font-medium text-orange-500 hover:text-orange-600"
                  >
                    <ExternalLink className="w-4 h-4" /> Live Demo
                  </a>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ProjectsSection;
