"use client";

import Image from "next/image";
import {
    Monitor,
    Globe,
    Smartphone,
    PenTool,
    BarChart,
    Code,
} from "lucide-react";
import { CircularProgress } from "@/components/ui/CircularProgress";
import type { FC, ReactNode } from "react";

// Service Item Component
type ServiceItemProps = {
    icon: ReactNode;
    title: string;
    description: string;
};

const ServiceItem: FC<ServiceItemProps> = ({ icon, title, description }) => (
    <div className="flex flex-col items-center text-center p-8 transition-colors hover:bg-card/90 h-full">
        <div className="mb-6 text-orange-500 p-3 border border-border rounded-full bg-card">
            {icon}
        </div>
        <h3 className="text-lg font-bold text-foreground mb-3">{title}</h3>
        <p className="text-sm text-muted-foreground leading-relaxed">{description}</p>
    </div>
);

// About Hero Section
const AboutHero = () => (
    <section
        id="about"
        className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center py-20 bg-card"
    >
        {/* Left: Image with frame */}
        <div className="relative flex justify-center lg:justify-end pr-0 lg:pr-12">
            <div className="relative w-72 h-80 sm:w-80 sm:h-96">
                <div className="absolute inset-0 border-[8px] border-background z-20 overflow-hidden rounded-md">
                    <Image
                        src="/images/dp1.png"
                        alt="Madhavi Sharma"
                        fill
                        className="object-cover grayscale-[15%] contrast-110"
                        priority
                    />
                </div>
                <div className="absolute -bottom-6 -left-6 w-24 h-24 bg-orange-500 z-10 clip-triangle" />
                <div className="absolute -bottom-3 -right-3 w-1/2 h-1/2 border-r-4 border-b-4 border-orange-400/30 z-0 rounded-bl-md" />
            </div>
            <style>{`
        .clip-triangle {
          clip-path: polygon(0 100%, 100% 100%, 0 0);
        }
      `}</style>
        </div>
        {/* Right: Text */}
        <div className="space-y-6 text-center lg:text-left">
            <div>
                <span className="text-muted-foreground uppercase tracking-widest text-sm font-semibold">
                    About Me
                </span>
                <h2 className="text-3xl sm:text-4xl font-bold text-foreground mt-2 leading-tight">
                    Why Hire Me for Your <br className="hidden sm:block" />
                    Next Project?
                </h2>
            </div>
            <p className="text-muted-foreground leading-relaxed max-w-xl mx-auto lg:mx-0 text-lg">
                I am Madhavi Sharma, a dedicated Software Developer helping businesses and individuals transform digital ideas into engaging products using modern web tools.
            </p>
            <p className="text-muted-foreground leading-relaxed max-w-xl mx-auto lg:mx-0 text-lg">
                My expertise lies in building scalable full-stack applications (React.js, Next.js, Node.js, TypeScript, MongoDB, Tailwind CSS, and more), delivering clean UI and robust backend solutions for startups and established teams alike.
            </p>
            <div className="pt-4">
                <a
                    href="#contact"
                    className="bg-gradient-to-r from-orange-500 to-pink-500 hover:from-orange-600 hover:to-pink-600 text-white font-semibold py-3 px-8 rounded shadow-lg transition-all transform hover:-translate-y-1"
                >
                    Let&apos;s Connect
                </a>
            </div>
        </div>
    </section>
);

// Services Data as const
const SERVICES = [
    {
        icon: <Monitor size={32} strokeWidth={1.5} />,
        title: "Web Designing",
        description: "Clean and pixel-perfect UI for every device.",
    },
    {
        icon: <Code size={32} strokeWidth={1.5} />,
        title: "Web Development",
        description: "Fast, scalable, and secure applications.",
    },
    {
        icon: <Smartphone size={32} strokeWidth={1.5} />,
        title: "App Development",
        description: "Modern mobile-friendly app development.",
    },
    {
        icon: <BarChart size={32} strokeWidth={1.5} />,
        title: "SEO Marketing",
        description: "Rank higher and boost visibility.",
    },
    {
        icon: <PenTool size={32} strokeWidth={1.5} />,
        title: "Creative Design",
        description: "Unique designs tailored to your brand.",
    },
    {
        icon: <Globe size={32} strokeWidth={1.5} />,
        title: "Global Marketing",
        description: "Grow your digital presence worldwide.",
    },




] as const;

// Services Section 
const ServicesSection = () => (
    <section className="py-20 flex justify-center">
        <div className="w-full max-w-6xl mx-auto">
            <div className="text-center mb-16">
                <span className="text-muted-foreground uppercase tracking-widest text-xs font-bold">
                    What I Do
                </span>
                <h2 className="text-3xl font-bold text-foreground mt-2">
                    Services &amp; Solutions
                </h2>
            </div>
            <div>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {SERVICES.map((service) => (
                        <div
                            key={service.title}
                            className="bg-card border border-border rounded-xl shadow-sm transition hover:shadow-lg flex flex-col h-full"
                        >
                            <ServiceItem {...service} />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    </section>
);

// Skills Data
const SKILLS = [
    { label: "Programming Languages", percentage: 87 },
    { label: "Frontend", percentage: 94 },
    { label: "Backend", percentage: 92 },
    { label: "Other Technologies", percentage: 81 },
];

// Skills Section
const SkillsSection = () => (
    <section className="bg-gradient-to-tr from-orange-50/60 to-pink-50/40 dark:from-orange-900/10 dark:to-pink-900/10 py-12 px-6 rounded-lg relative overflow-hidden my-20 border border-border max-w-6xl mx-auto">
        <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-transparent via-orange-400/20 to-transparent"></div>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-8 md:gap-12 justify-items-center relative">
            {SKILLS.map((skill) => (
                <div key={skill.label} className="flex flex-col items-center group">
                    <div className="relative flex items-center justify-center">
                        <CircularProgress
                            percentage={skill.percentage}
                            size={120}
                            strokeWidth={8}
                        />
                        <span className="absolute text-xl font-bold select-none">
                            {skill.percentage}%
                        </span>
                    </div>
                    <h3 className="mt-6 text-sm font-semibold tracking-wide text-muted-foreground group-hover:text-orange-500 transition-colors text-center">
                        {skill.label}
                    </h3>
                </div>
            ))}
        </div>
    </section>
);

// Main About Section
const AboutSection: FC = () => (
    <>
        <AboutHero />
        <ServicesSection />
        <SkillsSection />
    </>
);

export default AboutSection;
