"use client";

import Image from "next/image";
import { ArrowRight, Download } from "lucide-react";
import TypingAnimation from "@/components/typeing-animation";

export default function HeroSection() {
    return (
        <>
            {/* Hero Section */}
            <section
                id="home"
                className="min-h-screen flex items-center pt-8 bg-gradient-to-br from-background via-background to-accent/10"
            >
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-32 w-full py-16">
                    {/* Responsive: For small screens, stack and center text and image. On md+, two columns. */}
                    <div className="grid grid-cols-1 md:grid-cols-2 items-center md:gap-12 lg:gap-0">
                        {/* Left Side - Text */}
                        <div className="space-y-2 flex flex-col items-center md:items-start text-center md:text-left">
                            {/* Greeting and Name */}
                            <div>
                                <p className="text-xl sm:text-2xl text-muted-foreground font-semibold mb-1">
                                    Hello, My Name is
                                </p>
                                <h1 className="text-3xl sm:text-4xl md:text-6xl font-extrabold tracking-tight mb-2 bg-gradient-to-r from-orange-500 to-pink-500 bg-clip-text text-transparent">
                                    Madhavi Sharma{" "}
                                    <span className="text-foreground font-normal text-lg md:text-2xl align-middle">
                                        &nbsp;And I&apos;m a
                                    </span>
                                </h1>
                            </div>
                            {/* Typing Animation */}
                            <div className="text-2xl sm:text-3xl md:text-4xl font-bold">
                                <TypingAnimation />
                            </div>
                            <p className="text-xl sm:text-2xl text-muted-foreground/20 leading-relaxed font-medium">
                                Crafting fast, scalable, and impactful web experiences that drive results.
                            </p>
                            <div className="flex flex-col sm:flex-row gap-4 mt-2 justify-center md:justify-start">
                                <a
                                    href="#contact"
                                    className="inline-flex items-center gap-2 px-7 py-3 text-base bg-gradient-to-r from-orange-500 
                                            to-pink-500 hover:from-orange-600 hover:to-pink-600 text-white font-semibold rounded-xl 
                                            transition-all hover:shadow-lg"
                                >
                                    Get In Touch <ArrowRight className="w-5 h-5" />
                                </a>
                                <a
                                    href="/files/Madhavi_Sharma_Resume.pdf"
                                    download
                                    className="inline-flex items-center gap-2 px-7 py-3 text-base border border-orange-500 text-orange-500 font-semibold rounded-xl transition-all hover:bg-orange-50 dark:hover:bg-orange-900/20 hover:border-orange-600"
                                >
                                    <Download className="w-5 h-5" />
                                    Download CV
                                </a>
                            </div>
                        </div>

                        {/* Right Side - Animated Circular Image */}
                        <div className="relative flex justify-center md:justify-end h-[22rem] sm:h-[27rem] md:h-[30rem] md:h-full mt-10 md:mt-0">
                            <div className="relative w-64 h-64 sm:w-[22rem] sm:h-[22rem] md:w-[25rem] md:h-[25rem] flex items-center justify-center animate-bounce-smooth">
                                {/* BG: orange & pink gradient and highlight around circle */}
                                <div
                                    className="absolute inset-0 rounded-full z-0 pointer-events-none"
                                    style={{
                                        boxShadow:
                                            "0 15px 40px 0 rgba(251,113,133,0.13), 0 0 80px 12px rgba(249,115,22,0.18)",
                                        background: `
                                            radial-gradient(circle at 70% 30%, rgba(255,182,193,0.31) 0%, transparent 65%),
                                            radial-gradient(circle at 35% 70%, rgba(251,113,133,0.18) 0%, transparent 82%),
                                            linear-gradient(135deg, rgba(249,115,22,0.28) 0%, rgba(251,113,133,0.17) 80%)
                                        `,
                                        filter: "blur(1px)",
                                    }}
                                />
                                {/* The circular photo */}
                                <Image
                                    src="/images/dp1.png"
                                    alt="Developer"
                                    fill
                                    className="rounded-full object-cover shadow-xl z-10"
                                    style={{
                                        background:
                                            "linear-gradient(135deg, rgba(249,115,22,0.16) 0%, rgba(255,255,255,0.22) 40%, rgba(251,113,133,0.18) 100%)"
                                    }}
                                    priority
                                />
                            </div>
                            {/* Keyframe for bounce-smooth */}
                            <style jsx>{`
                                @keyframes bounce-smooth {
                                    0% {
                                        transform: translateY(0);
                                    }
                                    50% {
                                        transform: translateY(-24px);
                                    }
                                    100% {
                                        transform: translateY(0);
                                    }
                                }
                                .animate-bounce-smooth {
                                    animation: bounce-smooth 2.7s ease-in-out infinite;
                                }
                            `}</style>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
