"use client";

import React from "react";

const ContactSection = () => {
  return (
    <section id="contact" className="py-20 bg-background">
      <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-4xl font-bold mb-12 text-center">Get In Touch</h2>
        <form className="space-y-6 mb-12">
          <div>
            <label htmlFor="name" className="block text-sm font-medium mb-2">
              Name
            </label>
            <input
              type="text"
              id="name"
              className="w-full px-4 py-2 bg-card border border-border rounded-lg focus:outline-none focus:border-orange-500"
              placeholder="Your name"
            />
          </div>
          <div>
            <label htmlFor="email" className="block text-sm font-medium mb-2">
              Email
            </label>
            <input
              type="email"
              id="email"
              className="w-full px-4 py-2 bg-card border border-border rounded-lg focus:outline-none focus:border-orange-500"
              placeholder="your.email@example.com"
            />
          </div>
          <div>
            <label htmlFor="message" className="block text-sm font-medium mb-2">
              Message
            </label>
            <textarea
              id="message"
              rows={5}
              className="w-full px-4 py-2 bg-card border border-border rounded-lg focus:outline-none focus:border-orange-500 resize-none"
              placeholder="Your message"
            />
          </div>
          <button
            type="submit"
            className="w-full px-8 py-3 bg-gradient-to-r from-orange-500 to-pink-500 hover:from-orange-600 hover:to-pink-600 text-white font-semibold rounded-lg transition-all"
          >
            Send Message
          </button>
        </form>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 pt-8 border-t border-border">
          <div className="text-center">
            <p className="text-muted-foreground mb-2">Email</p>
            <a
              href="mailto:madhavi@example.com"
              className="text-orange-500 font-semibold hover:text-orange-600"
            >
              madhavi@example.com
            </a>
          </div>
          <div className="text-center">
            <p className="text-muted-foreground mb-2">Phone</p>
            <a
              href="tel:+919999999999"
              className="text-orange-500 font-semibold hover:text-orange-600"
            >
              +91 (999) 999-9999
            </a>
          </div>
          <div className="text-center">
            <p className="text-muted-foreground mb-2">Location</p>
            <p className="text-orange-500 font-semibold">Delhi, India</p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;
